import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import FixedAssetRevaluationCreateForm from "./createForm";

import notification from "../../../../components/notification";
// import { Input, Select, Divider } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/asset/fixedAssetRevaluation/actions";
import { Box } from "../../../App/app.style";
import { Tabs } from "antd";
import { getImportFixedAssets } from "../../../../redux/asset/fixedAssetRevaluation/saga";


const TabPane = Tabs.TabPane;

const redirectTo = "/dashboard/fixed-asset-revaluations"

class CreateFixedAssetRevaluation extends Component {
  // Get All data diawal
  componentDidMount() {
    const { createFixedAssetRevaluation, getImportFixedAssets } = this.props;
    createFixedAssetRevaluation();
    getImportFixedAssets();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.fixedAssetRevaluation.error) {
      notification("error", this.props.fixedAssetRevaluation.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.fixedAssetRevaluation.success) {
      this.props.history.replace(redirectTo + "/update/" + this.props.fixedAssetRevaluation.id_fixed_asset_revaluation)
    }
  }

  handleSubmit = value => {
    const { storeFixedAssetRevaluation } = this.props;
    //console.log(value)
    storeFixedAssetRevaluation(value);
  };

  render() {
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];
    // console.log(permissions);
    
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="fixedAssetRevaluations.add.title" />}
            style={{ margin:0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <FixedAssetRevaluationCreateForm
            submit={this.handleSubmit}
            fixedAssetRevaluation={this.props.fixedAssetRevaluation}
            permissions={permissions}
            getItems={this.props.getItems}
          />
          
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { 
    fixedAssetRevaluation: state.fixedAssetRevaluation,
    auth: state.auth
   };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(CreateFixedAssetRevaluation);
