import React, { Component } from "react";
import moment from "moment";
import { connect } from "react-redux";
import { compose } from "redux";
import actions from "../../../../redux/asset/fixedAssetRevaluation/actions";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import NumberFormat from "../../../App/NumberFormat";

import {
  Modal,
  Tooltip,
  Table,
  Collapse,
  Select,
  DatePicker,
  Input,
  Row,
  Col,
  Empty
} from "antd";
import {
  StatusTag,
  StatusBadge,
  TransNo,
  customCollapseStyle
} from "../../../App/app.style";
import getLanguage from "../../../App/MyLanguange";

const Panel = Collapse.Panel;
const Option = Select.Option;
const { RangePicker } = DatePicker;
const Search = Input.Search;
const { getImportFixedAssets } = actions;

const expandedRowRender = fixed_asset_expenditures => {
  const expenditureColumns = [
    {
      title: getLanguage({ id: "no." }),
      dataIndex: "index_no",
      width: "5%"
    },
    {
      title: getLanguage({ id: "accountCode" }),
      dataIndex: "account_code",
      width: "30%",
      render: text => {
        if (text && text.length > 25) {
          return (
            <Tooltip placement="bottom" title={text}>
              {text.substring(0, 25) + "..."}
            </Tooltip>
          );
        } else {
          return text;
        }
      }
    },
    {
      title: getLanguage({ id: "accountName" }),
      dataIndex: "account_name",
      width: "45%",
      render: (text, row) => {
        if (isNaN(row.index_no)) {
          return {
            props: {
              colSpan: 0
            }
          };
        }
        if (text && text.length > 50) {
          return (
            <Tooltip placement="bottom" title={text}>
              {text.substring(0, 50) + "..."}
            </Tooltip>
          );
        } else {
          return text;
        }
      }
    },
    {
      title: `${getLanguage({ id: "amount" })} (IDR)`,
      dataIndex: "amount",
      width: "20%",
      align: "right",
      render: text => <NumberFormat value={text} />
    }
  ];

  return (
    <div>
      <strong>
        <IntlMessages id="expenditureDetails" />
      </strong>

      <Table
        rowKey={record => record.index_no}
        size="small"
        columns={expenditureColumns}
        dataSource={fixed_asset_expenditures}
        pagination={true}
        style={{ marginTop: "24px" }}
      />
    </div>
  );
};

class ModalFixedAsset extends Component {
  state = {
    filter: {
      id_location: null,
      id_department: null,
      id_asset_type: null,
      id_asset_category: null,
      status: null,
      start_date: null,
      end_date: null,
      pagination: null
    },
    search: null
  };

  handleSelect = value => {
    const { handleSelectModal } = this.props;
    handleSelectModal(value);
  };

  handleLocationChange = id_location => {
    const { getImportFixedAssets } = this.props;
    const string = "id_location=" + id_location;
    getImportFixedAssets(1, null, string);
  };

  // componentDidUpdate(prevProps) {
  //   const { id_location } = this.props;

  //   if (prevProps.id_location !== id_location) {
  //     this.handleLocationChange(id_location);
  //     this.setState({
  //       filter: {
  //         ...this.state.filter,
  //         id_location: id_location
  //       }
  //     });
  //   }
  // }
  // Handle search
  handleSearch = value => {
    const { getImportFixedAssets } = this.props;
    this.setState({
      filter: {
        id_location: null,
        id_department: null,
        id_asset_type: null,
        id_asset_category: null,
        status: null,
        start_date: null,
        end_date: null,
        pagination: null
      }
    });
    getImportFixedAssets(1, value);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getImportFixedAssets } = this.props;
    this.setState({
      filter: {
        id_location: null,
        id_department: null,
        id_asset_type: null,
        id_asset_category: null,
        status: null,
        start_date: null,
        end_date: null,
        pagination: null
      }
    });
    this.setState({ search: null });
    getImportFixedAssets();
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  // Handle filter
  handleFilter = () => {
    const { getImportFixedAssets } = this.props;
    //const { current_page } = this.props.fixedAsset;
    const { filter } = this.state;
    let string = "";

    if (filter.id_location) {
      string += "id_location=" + filter.id_location;
    }
    if (filter.id_department) {
      string
        ? (string += "&id_department=" + filter.id_department)
        : (string += "id_department=" + filter.id_department);
    }
    if (filter.id_asset_type) {
      string
        ? (string += "&id_asset_type=" + filter.id_asset_type)
        : (string += "id_asset_type=" + filter.id_asset_type);
    }
    if (filter.id_asset_category) {
      string
        ? (string += "&id_asset_category=" + filter.id_asset_category)
        : (string += "id_asset_category=" + filter.id_asset_category);
    }
    if (filter.status) {
      string
        ? (string += "&status=" + filter.status)
        : (string += "status=" + filter.status);
    }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
            "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
            "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }

    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }

    this.setState({ search: null });
    getImportFixedAssets(1, null, string);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getImportFixedAssets } = this.props;
    this.setState({
      filter: {
        id_location: null,
        id_department: null,
        id_asset_type: null,
        id_asset_category: null,
        status: null,
        start_date: null,
        end_date: null,
        pagination: null
      }
    });
    this.setState({ search: null });
    getImportFixedAssets();
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activateFixedAsset(id);
    } else {
      this.props.deactivateFixedAsset(id);
    }
  };

  // Handle pagination
  handlePageChange = page => {
    const { getImportFixedAssets } = this.props;
    const { import_search, import_filter } = this.props.fixedAssetRevaluation;
    getImportFixedAssets(page, import_search, import_filter);
  };

  render() {
    // console.log(this.props.fixedAssetRevaluation);

    const {
      visible,
      handleModal,
      fixedAssetRevaluation,
      id_location
    } = this.props;
    const {
      import_fixed_assets,
      locations,
      departments
    } = fixedAssetRevaluation ? fixedAssetRevaluation : [];
    const { loadingImport } = fixedAssetRevaluation
      ? fixedAssetRevaluation
      : false;
    const dataSource = [];
    Object.keys(import_fixed_assets).map((fixed_asset, index) => {
      return dataSource.push({
        ...import_fixed_assets[fixed_asset],
        key: fixed_asset
      });
    });

    // Definisi kolom2 serta field2 API yang dipakai tabel
    let columns = [
      {
        title: getLanguage({ id: "code" }),
        dataIndex: "fixed_asset_code",
        rowKey: "fixed_asset_code",
        width: "10%",
        render: (text, row) => {
          let className;
          switch (row.status) {
            case 1:
              className = "new";
              break;
            case 2:
              className = "ordered";
              break;
            // case 2:
            //   className = "closed";
            //   break;
            case 3:
              className = "cancelled";
              break;
            default:
              className = "new";
              break;
          }

          return (
            <div style={{ display: "flex", height: "20px" }}>
              <StatusBadge className={className} />
              <Tooltip
                placement="bottom"
                title={<IntlMessages id="tooltip.select" />}
              >
                <a
                  onClick={e => {
                    e.preventDefault();
                    this.handleSelect(row.id_fixed_asset);
                  }}
                  href="javascript:;"
                >
                  {text ? (
                    <TransNo>{text}</TransNo>
                  ) : (
                    <TransNo>{getLanguage({ id: "new" })} </TransNo>
                  )}
                </a>
              </Tooltip>
            </div>
          );
        }
      },
      {
        title: getLanguage({ id: "acquisitionDate" }),
        dataIndex: "acquisition_date",
        rowKey: "acquisition_date",
        width: "15%",
        render: text => (
          <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
        )
      },
      {
        title: getLanguage({ id: "name" }),
        dataIndex: "fixed_asset_name",
        rowKey: "fixed_asset_name",
        width: "10%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "fixedAssets.add.serialNo" }),
        dataIndex: "serial_no",
        rowKey: "serial_no",
        width: "10%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "sidebar.location" }),
        dataIndex: "location.location_name",
        rowKey: "location.location_name",
        width: "20%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "sidebar.department" }),
        dataIndex: "department.department_name",
        rowKey: "department.department_name",
        width: "20%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "createdBy" }),
        dataIndex: "created_by",
        rowKey: "created_by",
        width: "20%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "status",
        rowKey: "status",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          switch (text) {
            case 1:
              className = "new";
              display = getLanguage({ id: "new" });
              break;
            case 2:
              className = "ordered";
              display = getLanguage({ id: "processed" });
              break;
            // case 2:
            //   className = "closed";
            //   display = "Closed";
            //   break;
            case 3:
              className = "cancelled";
              display = getLanguage({ id: "cancelled" });
              break;
            default:
              className = "new";
              display = getLanguage({ id: "new" });
              break;
          }

          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }
    ];

    const {
      import_current_page,
      import_page_size,
      import_total
    } = this.props.fixedAssetRevaluation;

    return (
      <Modal
        title={<IntlMessages id="fixedAssetRevaluations.import" />}
        visible={visible}
        centered
        footer={null}
        keyboard={true}
        onCancel={handleModal}
        width="95%"
      >
        <Row style={{ marginBottom: "10px" }} type="flex">
          <Col span={18}>
            <Search
              placeholder={
                getLanguage({ id: "code" }) +
                ", " +
                getLanguage({ id: "name" }) +
                ", " +
                getLanguage({ id: "fixedAssets.add.serialNo" }) +
                ", " +
                getLanguage({ id: "createdBy" })
              }
              onSearch={this.handleSearch}
              style={{ width: "99%", height: "36px" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
          </Col>
          <Col span={6}>
            <Button
              type="primary"
              onClick={this.handleSearch.bind(this, this.state.search)}
              style={{ marginRight: "2%" }}
            >
              <IntlMessages id="button.search" />
            </Button>

            <Button onClick={this.handleResetSearch}>
              <IntlMessages id="filter.reset" />
            </Button>
          </Col>
        </Row>
        <Row style={{ marginBottom: "10px" }}>
          <Col>
            <Collapse style={customCollapseStyle}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.state.filter.id_location
                          ? this.state.filter.id_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="fixedAssets.filter.select.location" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingImport}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {data.location_code + " (" + data.location_name + ")"}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                  <Col span={12}>
                    <label>
                      <IntlMessages id="employees.filter.departments" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_department"
                      )}
                      value={
                        this.state.filter.id_department
                          ? this.state.filter.id_department
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="employees.filter.select.department" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingImport}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {departments.map((data, key) => (
                        <Option key={key} value={data.id_department}>
                          {data.department_name}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                </Row>
                <Row type="flex" justify="start">
                  <Col span={12}>
                    <label>
                      <IntlMessages id="fixedAssets.filter.date" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime
                      allowClear={true}
                      style={{ width: "95%" }}
                      value={
                        this.state.filter.start_date &&
                        this.state.filter.end_date
                          ? [
                              moment(this.state.filter.start_date),
                              moment(this.state.filter.end_date)
                            ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "date")}
                    />
                  </Col>
                  <Col span={4}>
                    <Row justify="space-between" gutter={1} type="flex">
                      {/* <Col span={12}>
                        <label>Status</label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "status"
                          )}
                          value={
                            this.state.filter.status
                              ? this.state.filter.status
                              : []
                          }
                          style={{ width: "90%" }}
                          placeholder="Status"
                          optionFilterProp="children"
                          filterOption={true}
                          // loading={this.props.location.loadingFilterSites}
                          allowClear={true}
                          //showArrow={false}
                        >
                          <Option value={1}>New</Option>
                          <Option value={2}>Processed</Option>
                          <Option value={3}>Cancelled</Option>
                        </Select>
                      </Col> */}
                      <Col span={24}>
                        <label>
                          <IntlMessages id="dataPerPage" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "pagination"
                          )}
                          value={
                            this.state.filter.pagination
                              ? this.state.filter.pagination
                              : import_page_size
                          }
                          style={{ width: "90%" }}
                          // placeholder={
                          //   <IntlMessages id="pettyCashTypes.filter.select.transactionType" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value={10}>
                            10
                          </Option>
                          <Option key={2} value={25}>
                            25
                          </Option>
                          <Option key={3} value={50}>
                            50
                          </Option>
                          <Option key={4} value={100}>
                            100
                          </Option>
                        </Select>
                      </Col>
                    </Row>
                  </Col>
                  <Col
                    span={6}
                    style={{
                      marginTop: "auto"
                    }}
                  >
                    <Button
                      size="small"
                      type="primary"
                      style={{ marginRight: "10px" }}
                      onClick={this.handleFilter}
                    >
                      <IntlMessages id="filter.submit" />
                    </Button>
                    <Button onClick={this.handleResetFilter} size="small">
                      <IntlMessages id="filter.reset" />
                    </Button>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </Col>
        </Row>

        <Row>
          <Col>
            {dataSource.length === 0 && !loadingImport ? (
              <Empty
                description={<IntlMessages id="fixedAssets.table.noData" />}
              />
            ) : (
              <Table
                dataSource={dataSource}
                columns={columns}
                loading={loadingImport}
                expandedRowRender={record =>
                  expandedRowRender(record.fixed_asset_expenditures)
                }
                expandIcon={props => (
                  <Button
                    onClick={e => props.onExpand(props.record, e)}
                    size="small"
                  >
                    <IntlMessages id="button.view" />
                  </Button>
                )}
                pagination={
                  import_page_size
                    ? {
                        current: import_current_page,
                        pageSize: parseInt(import_page_size),
                        total: import_total,
                        onChange: this.handlePageChange.bind(this)
                      }
                    : true
                }
              />
            )}
          </Col>
        </Row>
      </Modal>
    );
  }
}

export default compose(connect(null, { getImportFixedAssets }))(
  ModalFixedAsset
);
