import React, { Component } from "react";

import moment from "moment"

//import EditableTable from "./editableTable";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import {
  Form, Input, Select, Col, InputNumber, DatePicker,
  Radio, Divider, Tabs,
  Tooltip, Icon, Row
}
  from "antd";
//import notification from "../../../../components/notification";
import ModalFixedAsset from "./modalFixedAsset";

// const TabPane = Tabs.TabPane;

const { Option } = Select;
const InputGroup = Input.Group;
const TextArea = Input.TextArea;

class FixedAssetRevaluationCreateForm extends Component {
  state = {
    id_fixed_asset: null,
    visibleModalFixedAsset: false,
    id_location: null,
  }

  handleModalFixedAsset = () => {
    // console.log("masuk")
    this.setState({
      visibleModalFixedAsset: !this.state.visibleModalFixedAsset
    })
  };

  handleSelectLocation = id_location => {
    const { setFieldsValue } = this.props.form;

    if(id_location !== this.state.id_location){
      setFieldsValue({
        id_account_sales: null
      })
    }

    this.setState({
      id_location,
      //clear: this.state.id_location !== id_location ? true : false
    });

  };

  handleChangeTransactionType = e => {
    const { setFieldsValue, getFieldValue } = this.props.form;

    if (e === 1) {
      setFieldsValue({
        depreciation_value: 0,
      })
    }
    else {
      setFieldsValue({
        amount: 0,
        depreciation_value: 0,
      })

      this.handleChangeQuantity(getFieldValue("qty"));
    }
  }

  handleSelectModalFixedAsset = value => {
    const { import_fixed_assets } = this.props.fixedAssetRevaluation;
    const { getFieldValue, setFieldsValue } = this.props.form;


    let [import_fixed_asset] = import_fixed_assets.filter(
      curr_data => curr_data.id_fixed_asset === value
    );

    let estimated_life = import_fixed_asset.estimated_life_year * 12 + import_fixed_asset.estimated_life_month
    let asset_age = moment(new Date(), "YYYY-MM-DD").diff(moment(import_fixed_asset.acquisition_date, "YYYY-MM-DD"), "months")


    setFieldsValue({
      id_location: import_fixed_asset.id_location,
      id_department: import_fixed_asset.id_department,
      fixed_asset_code: import_fixed_asset.fixed_asset_code,
      fixed_asset_name: import_fixed_asset.fixed_asset_name,
      serial_no: import_fixed_asset.serial_no,
      asset_value: parseFloat(import_fixed_asset.asset_value),
      //acquisition_date: moment(import_fixed_asset.acquisition_date, "YYYY-MM-DD HH:mm:ss"),
      acquisition_date: moment(import_fixed_asset.acquisition_date, "YYYY-MM-DD"),
      depreciation_end_date: moment(import_fixed_asset.depreciation_end_date, "YYYY-MM-DD"),
      asset_age: estimated_life - asset_age > 0 ? asset_age : estimated_life,
      estimated_life,
      qty_fa: import_fixed_asset.qty,
      monthly_depreciation: import_fixed_asset.monthly_depreciation,
      book_value: parseFloat(import_fixed_asset.book_value),
      depreciation_value_from_fa: parseFloat(import_fixed_asset.depreciation_value),
      transaction_date: getFieldValue("transaction_date") < moment(import_fixed_asset.acquisition_date, "YYYY-MM-DD") 
        ? moment()
        : getFieldValue("transaction_date")
    })

    this.setState({
      id_fixed_asset: value
    })

    this.handleChangeQuantity(getFieldValue("qty"));
    this.handleModalFixedAsset();
    this.handleSelectLocation(import_fixed_asset.id_location)
  }

  amountValue = (qty, all_qty, asset_value) => {
    if (qty === undefined || all_qty === undefined || asset_value === undefined) {
      return 0
    }
    else {
      return qty / all_qty * asset_value
    }
  }

  depreciationValue = (qty, all_qty, depreciation) => {
    if (qty === undefined || all_qty === undefined || depreciation === undefined) {
      return 0
    }
    else {
      return qty / all_qty * depreciation
    }
  }

  handleChangeQuantity = qty => {
    const { getFieldValue, setFieldsValue } = this.props.form;
    let amount = this.amountValue(qty, getFieldValue('qty_fa'), getFieldValue('asset_value'))
    let depreciation_value = this.depreciationValue(qty, getFieldValue('qty_fa'), getFieldValue('monthly_depreciation'))

    setFieldsValue({
      amount,
      depreciation_value
    })
  }


  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        const { sub_ledger_accounts } = this.props.fixedAssetRevaluation

        if (values.id_account_sales) {
          const account = sub_ledger_accounts.find(
            data => (data.account_code + " " + data.account_name) === values.id_account_sales
          );

          values.id_account_sales = account.id_account;
          values.account_code_sales = account.account_code;
          values.account_name_sales = account.account_name;
          values.id_sub_ledger = account.id_sub_ledger;
        }

        values.transaction_date = values.transaction_date.format("YYYY-MM-DD HH:mm:ss");
        values.id_fixed_asset = this.state.id_fixed_asset;

        this.props.submit(values);
      }
    });
  };

  render() {
    //console.log(this.props.fixedAssetRevaluation);
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const { fixedAssetRevaluation } = this.props;
    const { loading, loadingImport, base_currency } = fixedAssetRevaluation;

    let permissions = this.props.permissions;

    //const error = fixedAssetRevaluation.errorField ? fixedAssetRevaluation.errorField : [];
    //console.log(this.state.id_location, getFieldValue('id_account_sales'));

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <ModalFixedAsset
          visible={this.state.visibleModalFixedAsset}
          fixedAssetRevaluation={fixedAssetRevaluation}
          id_location={this.state.id_location}
          handleModal={this.handleModalFixedAsset}
          handleSelectModal={this.handleSelectModalFixedAsset}
        />
        <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="fixedAssetRevaluation.subtitle.fixedAsset" />
        </Divider>

        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
            <Form.Item
              {...formItemLayout}
              label={
                <Tooltip
                  placement="top"
                  title={
                    <IntlMessages id="fixedAssetRevaluations.import.help" />
                  }
                >
                  <IntlMessages id="fixedAssetRevaluations.import" />
                  <Icon
                    style={{ marginLeft: "5px" }}
                    type="question-circle"
                    theme="filled"
                  />
                </Tooltip>
              }
            >
              <Button
                icon="select"
                loading={loadingImport}
                disabled={loadingImport}
                onClick={this.handleModalFixedAsset}
                style={{ height: "31px", width: "100%" }}
              // shape="circle"
              >
                <IntlMessages id="button.import" />
              </Button>
            </Form.Item>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="locations.pageHeader" />}
                >
                  {getFieldDecorator("id_location", {
                    // initialValue: data.id_location,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.fixedAssetRevaluation.loading}
                      showSearch
                      optionFilterProp="children"
                      // onChange={this.handleSelectLocation}
                      style={{ width: "100%" }}
                      disabled={true}
                    >
                      {fixedAssetRevaluation.locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {data.location_code + " (" + data.location_name + ")"}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="departments.pageHeader" />}
                >
                  {getFieldDecorator("id_department", {
                    // initialValue: data.id_department,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.fixedAssetRevaluation.loading}
                      showSearch
                      optionFilterProp="children"
                      style={{ width: "100%" }}
                      disabled={true}
                    >

                      {fixedAssetRevaluation.departments.map((data, key) => (
                        <Option key={key} value={data.id_department}>
                          {data.department_code + " (" + data.department_name + ")"}
                        </Option>
                      ))
                      }
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="fixedAssets.add.code" />}
                >
                  {getFieldDecorator("fixed_asset_code", {
                    //initialValue: data.fixed_asset_code,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      },
                      {
                        max: 30,
                        message: <IntlMessages id="maxLength30" />
                      }
                    ]
                  })(
                    <Input
                      disabled={true}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="fixedAssetRevaluations.add.name" />}
                >
                  {getFieldDecorator("fixed_asset_name", {
                    //initialValue: data.fixed_asset_name,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      },
                      {
                        max: 50,
                        message: <IntlMessages id="maxLength50" />
                      }
                    ]
                  })(<Input
                    disabled={true}
                  />)}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="fixedAssetRevaluations.add.serialNo" />}
                >
                  {getFieldDecorator("serial_no", {
                    //initialValue: data.serial_no,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      },
                      {
                        max: 50,
                        message: <IntlMessages id="maxLength50" />
                      }
                    ]
                  })(<Input
                    disabled={true}
                  />)}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="assetValue" />}
                >
                  {getFieldDecorator("asset_value", {
                    //initialValue: 0,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      min={0}
                      precision={2}
                      disabled={true}
                      loading={loading}
                      style={{ width: "100%" }}
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>



          </div>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 0 0 15px" }}>

            <InputGroup style={{ marginBottom: "-5px" }}>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    <IntlMessages id="acquisitionDate" />
                  }
                >
                  {getFieldDecorator("acquisition_date", {
                    //initialValue: moment(),
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <DatePicker
                      style={{ width: "100%", padding: "0px" }}
                      format="DD MMMM YYYY"
                      disabled={true}
                      allowClear={false}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    <IntlMessages id="depreciationEndDate" />
                  }
                >
                  {getFieldDecorator("depreciation_end_date", {
                    //initialValue: moment().add(12, "month"),
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <DatePicker
                      style={{ width: "100%", padding: "0px" }}
                      format="DD MMMM YYYY"
                      allowClear={false}
                      disabled={true}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="assetAge" />}
                >
                  {getFieldDecorator("asset_age", {
                    //initialValue: data.fixed_asset_name,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      },
                    ]
                  })(
                    <InputNumber
                      disabled={true}
                      loading={loading}
                      style={{ width: "100%" }}
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="estimatedLife" />}
                >
                  {getFieldDecorator("estimated_life", {
                    //initialValue: data.estimated_life,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      },
                    ]
                  })(
                    <InputNumber
                      disabled={true}
                      loading={loading}
                      style={{ width: "100%" }}
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="quantity" />}
                >
                  {getFieldDecorator("qty_fa", {
                    //initialValue: 1,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      disabled={true}
                      min={0}
                      precision={2}
                      loading={loading}
                      style={{ width: "100%" }}
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="monthlyDepreciation" />}
                >
                  {getFieldDecorator("monthly_depreciation", {
                    //initialValue: 0,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      disabled={true}
                      //onChange= {this.handleChangeFromEstimatedLifeYear}
                      min={0}
                      precision={2}
                      loading={loading}
                      style={{ width: "100%" }}
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="bookValue" />}
                >
                  {getFieldDecorator("book_value", {
                    //initialValue: 1,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      disabled={true}
                      min={0}
                      precision={2}
                      loading={loading}
                      style={{ width: "100%" }}
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="accumulationOfDepreciation" />}
                >
                  {getFieldDecorator("depreciation_value_from_fa", {
                    //initialValue: 0,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      disabled={true}
                      min={0}
                      precision={2}
                      loading={loading}
                      style={{ width: "100%" }}
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

          </div>
        </div>

        <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="fixedAssetRevaluation.subtitle.revaluation" />
        </Divider>

        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
            <Row style={{ marginBottom: "-5px" }}>
              <Col span={24}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    <IntlMessages id="purchaseOrders.transactionDate" />
                  }
                >
                  {getFieldDecorator("transaction_date", {
                    initialValue: moment(),
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <DatePicker
                      style={{ width: "100%" }}
                      showTime
                      format="DD MMMM YYYY HH:mm:ss"
                      onChange={this.handleChangeTransDate}
                      onOpenChange={this.handleChangeTransDate}
                      disabled={
                        !permissions.includes("Can Back Date")
                          ? true
                          : false
                      }
                      allowClear={false}
                      disabledDate={e => 
                        getFieldValue("acquisition_date")
                          ? e.valueOf() > moment() || e.valueOf() < moment(getFieldValue("acquisition_date"), "YYYY-MM-DD HH:mm:ss")  
                          : e.valueOf() > moment()       
                      }
                    />
                  )}
                </Form.Item>
              </Col>
              {/* <Col span={12}>
                
              </Col> */}
            </Row>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="transactionType" />}
            >
              {getFieldDecorator("transaction_type", {
                initialValue: 2,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                ]
              })(
                <Radio.Group
                  style={{ display: "flex" }}
                  onChange={this.handleChangeTransactionType}
                  buttonStyle="solid"
                  loading={loading}
                  style={{ display: "flex" }}
                >
                  {/* <Radio.Button value={1}><IntlMessages id="valueAdd" /></Radio.Button> */}
                  <Radio.Button style={{ flex: "1", textAlign: "center" }} value={2}><IntlMessages id="disposal" /></Radio.Button>
                  <Radio.Button style={{ flex: "1", textAlign: "center" }} value={3}><IntlMessages id="fixedAssetSale" /></Radio.Button>
                </Radio.Group>
              )}
            </Form.Item>

            {
              getFieldValue("transaction_type") === 1
                ?
                (
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="amount" />}
                  >
                    {getFieldDecorator("amount", {
                      //initialValue: 0,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <InputNumber
                        //min={0}
                        precision={2}
                        loading={loading}
                        style={{ width: "100%" }}
                        formatter={value =>
                          `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                        }
                        parser={value => value.replace(/\$\s?|(,*)/g, "")}
                      />
                    )}
                  </Form.Item>
                )
                :
                (
                  <InputGroup>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="quantity" />}
                      >
                        {getFieldDecorator("qty", {
                          initialValue: 0,
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(
                          <InputNumber
                            //disabled={true}
                            min={0}
                            max={getFieldValue('qty_fa') ? parseFloat(getFieldValue('qty_fa')) : 999999999999999}
                            precision={2}
                            onChange={this.handleChangeQuantity}
                            loading={loading}
                            style={{ width: "100%" }}
                            formatter={value =>
                              `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                            }
                            parser={value => value.replace(/\$\s?|(,*)/g, "")}
                          />
                        )}
                      </Form.Item>
                    </Col>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="amount" />}
                      >
                        {getFieldDecorator("amount", {
                          //initialValue: 0,
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(
                          <InputNumber
                            disabled={true}
                            min={0}
                            precision={2}
                            loading={loading}
                            style={{ width: "100%" }}
                            formatter={value =>
                              `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                            }
                            parser={value => value.replace(/\$\s?|(,*)/g, "")}
                          />
                        )}
                      </Form.Item>
                    </Col>
                  </InputGroup>
                )
            }


          </div>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 0 0 15px" }}>
            {
              getFieldValue("transaction_type") !== 1
                ?
                (
                  <InputGroup>
                    {/* <Col span={12}>
                        <Form.Item
                          {...formItemLayout}
                          label={<IntlMessages id="depreciation" />}
                        >
                          {getFieldDecorator("depreciation_value", {
                            initialValue: 0,
                            rules: [
                              {
                                required: true,
                                message: <IntlMessages id="required" />
                              }
                            ]
                          })(
                            <InputNumber
                              disabled={true}
                              min={0}
                              precision={2}
                              loading={loading}
                              style={{ width:"100%" }}
                              formatter={value =>
                                `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                              }
                              parser={value => value.replace(/\$\s?|(,*)/g, "")}
                            />
                          )}
                        </Form.Item>
                      </Col> */}
                    <Col span={24}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="gainLossExpendAccount" />}
                      >
                        {getFieldDecorator("id_account_gain_loss", {
                          // initialValue: data.id_account_gain_loss,
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(
                          <Select
                            loading={this.props.fixedAssetRevaluation.loading}
                            showSearch
                            optionFilterProp="children"
                            style={{ width: "100%" }}
                          // disabled={used ? true : false}
                          >
                            {this.props.fixedAssetRevaluation.accounts.map((data, key) => (
                              data.id_account_type === "16" ||
                                data.id_account_type === "17" ||
                                data.id_account_type === "18"
                                ? (<Option key={key} value={data.id_account}>
                                  {data.account_code + " (" + data.account_name + ")"}
                                </Option>)
                                : null
                            ))}
                          </Select>
                        )}
                      </Form.Item>
                    </Col>
                  </InputGroup>
                )
                :
                (
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="gainLossExpendAccount" />}
                  >
                    {getFieldDecorator("id_account_gain_loss", {
                      // initialValue: data.id_account_gain_loss,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <Select
                        loading={this.props.fixedAssetRevaluation.loading}
                        showSearch
                        optionFilterProp="children"
                        style={{ width: "100%" }}
                      // disabled={used ? true : false}
                      >
                        {this.props.fixedAssetRevaluation.accounts.map((data, key) => (
                          //data.id_account_type === "06"
                          data.id_account_type
                            ? (<Option key={key} value={data.id_account}>
                              {data.account_code + " (" + data.account_name + ")"}
                            </Option>)
                            : null
                        ))}
                      </Select>
                    )}
                  </Form.Item>
                )
            }



            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="note" />}
            >
              {getFieldDecorator("note", {
                //initialValue: data.note,
              })(<TextArea
                autosize={{ minRows: 4 }}
              />)}
            </Form.Item>
          </div>
        </div>

        {
          getFieldValue("transaction_type") === 3
            ?
            (
              <>
                <Divider orientation="left" style={{ fontSize: 14 }}>
                  <IntlMessages id="fixedAssetRevaluation.subtitle.sales" />
                </Divider>

                <div className="isoDetailDataFlex" style={{ padding: "0" }}>
                  <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
                    <InputGroup>
                      <Col span={12}>
                        <Form.Item
                          {...formItemLayout}
                          label={<IntlMessages id="salesReferenceNo" />}
                        >
                          {getFieldDecorator("sales_reference_no", {
                            //initialValue: 1,
                            rules: [
                              {
                                required: true,
                                message: <IntlMessages id="required" />
                              }
                            ]
                          })(
                            <Input
                              //loading={loading}
                              style={{ width: "100%" }}
                            />
                          )}
                        </Form.Item>
                      </Col>
                      <Col span={12}>
                        <Form.Item
                          {...formItemLayout}
                          label={<IntlMessages id="salesPrice" />}
                        >
                          {getFieldDecorator("sales_price", {
                            //initialValue: 0,
                            rules: [
                              {
                                required: true,
                                message: <IntlMessages id="required" />
                              }
                            ]
                          })(
                            <InputNumber
                              min={0}
                              precision={2}
                              loading={loading}
                              style={{ width: "100%" }}
                              formatter={value =>
                                `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                              }
                              parser={value => value.replace(/\$\s?|(,*)/g, "")}
                            />
                          )}
                        </Form.Item>
                      </Col>
                    </InputGroup>
                  </div>
                  <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 0 0 15px" }}>
                    <InputGroup>
                      <Col span={24}>
                        <Form.Item
                          {...formItemLayout}
                          label={<IntlMessages id="paymentAccount" />}
                        >
                          {getFieldDecorator("id_account_sales", {
                            //initialValue: data.account_code_sales + " " + data.account_name_sales,
                            rules: [
                              {
                                required: true,
                                message: <IntlMessages id="required" />
                              }
                            ]
                          })(
                            <Select
                              loading={this.props.fixedAssetRevaluation.loading}
                              showSearch
                              optionFilterProp="children"
                              style={{ width: "100%" }}
                            // disabled={used ? true : false}
                            >
                              {this.props.fixedAssetRevaluation.sub_ledger_accounts.map((data, key) => (
                                data.id_account_type === "02" &&
                                  data.id_location === getFieldValue('id_location') &&
                                  data.id_currency === base_currency.id_currency
                                  ? (<Option key={key} value={data.account_code + " " + data.account_name}>
                                    {data.account_code + " " + data.account_name}
                                  </Option>)
                                  : null
                              ))}
                            </Select>
                          )}
                        </Form.Item>
                      </Col>
                      {/* <Col span={12}>
                        
                      </Col> */}
                    </InputGroup>
                  </div>
                </div>
              </>
            )
            : null
        }

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginLeft: "50px" }}
            loading={loading}
          >
            <IntlMessages id="button.submit" />
          </Button>
        </Form.Item>
      </Form>
    );
  }
}

const WrappedFixedAssetRevaluationCreateForm = Form.create({ name: "create_fixed_asset" })(FixedAssetRevaluationCreateForm);

export default WrappedFixedAssetRevaluationCreateForm;
