const actions = {
  getFixedAssetRevaluations: (page, search, filter) => ({
    type: 'GET_FIXED_ASSET_REVALUATIONS_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  getImportFixedAssets: (page, search, filter) => ({
    type: 'GET_IMPORT_FIXED_ASSETS_FAR_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createFixedAssetRevaluation: () => ({
    type: 'CREATE_FIXED_ASSET_REVALUATION_REQUEST'
  }),
  storeFixedAssetRevaluation: data => ({
    type: 'STORE_FIXED_ASSET_REVALUATION_REQUEST',
    payload: { body: data }
  }),
  editFixedAssetRevaluation: id_fixed_asset_revaluation => ({
    type: 'EDIT_FIXED_ASSET_REVALUATION_REQUEST',
    payload: { id_fixed_asset_revaluation: id_fixed_asset_revaluation }
  }),
  updateFixedAssetRevaluation: (id_fixed_asset_revaluation, data) => ({
    type: 'UPDATE_FIXED_ASSET_REVALUATION_REQUEST',
    payload: {
      id_fixed_asset_revaluation: id_fixed_asset_revaluation,
      body: data
    }
  }),
  showFixedAssetRevaluation: id_fixed_asset_revaluation => ({
    type: 'SHOW_FIXED_ASSET_REVALUATION_REQUEST',
    payload: { id_fixed_asset_revaluation: id_fixed_asset_revaluation }
  }),
  processFixedAssetRevaluation: id_fixed_asset_revaluation => ({
    type: "PROCESS_FIXED_ASSET_REVALUATION_REQUEST",
    payload: { id_fixed_asset_revaluation }
  }),
  cancelFixedAssetRevaluation: (id_fixed_asset_revaluation, data) => ({
    type: "CANCEL_FIXED_ASSET_REVALUATION_REQUEST",
    payload: { 
      id_fixed_asset_revaluation,
      body : data
    }
  }),
  uploadDocument: (id_fixed_asset_revaluation, data) => ({
    type: "UPLOAD_FIXED_ASSET_REVALUATION_DOCUMENT_REQUEST",
    payload: {
      id_fixed_asset_revaluation,
      body : data
    }
  }),
  downloadDocument: id_fixed_asset_revaluation_document => ({
    type: "DOWNLOAD_FIXED_ASSET_REVALUATION_DOCUMENT_REQUEST",
    payload: {id_fixed_asset_revaluation_document}
  }),
  deleteDocument: id_fixed_asset_revaluation_document => ({
    type: "DELETE_FIXED_ASSET_REVALUATION_DOCUMENT_REQUEST",
    payload: { id_fixed_asset_revaluation_document }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
